import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  Alert,
  IconButton,
  InputAdornment,
} from '@mui/material';
import {
  VpnKey,
  Visibility,
  VisibilityOff,
  Close,
  Save,
} from '@mui/icons-material';
import { saveAPIConfig, getAPIConfig } from '../config/api';

// ===========================================
// MinutesGen v1.0 - API
// ===========================================

interface APIKeySetupProps {
  open: boolean;
  onClose: () => void;
  onComplete: () => void;
}

export const APIKeySetup: React.FC<APIKeySetupProps> = ({ open, onClose, onComplete }) => {
  const [apiKey, setApiKey] = useState('');
  const [showApiKey, setShowApiKey] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isSaving, setIsSaving] = useState(false);

  const handleSave = async () => {
    if (!apiKey.trim()) {
      setError('API');
      return;
    }

    if (!apiKey.startsWith('sk-')) {
      setError('OpenAI APIsk-');
      return;
    }

    setIsSaving(true);
    setError(null);

    try {
      // API
      const currentConfig = getAPIConfig();
      const updatedConfig = {
        ...currentConfig,
        openaiApiKey: apiKey.trim(),
      };

      saveAPIConfig(updatedConfig);
      onComplete();
      onClose();
    } catch (error) {
      setError('');
    } finally {
      setIsSaving(false);
    }
  };

  const handleClose = () => {
    setApiKey('');
    setError(null);
    onClose();
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      maxWidth="sm"
      fullWidth
      disableEscapeKeyDown
      PaperProps={{
        sx: {
          borderRadius: 2,
        },
      }}
    >
      <DialogTitle sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <VpnKey sx={{ mr: 1, color: 'primary.main' }} />
          <Typography variant="h6">OpenAI API</Typography>
        </Box>
        <IconButton onClick={handleClose} size="small">
          <Close />
        </IconButton>
      </DialogTitle>

      <DialogContent dividers sx={{ p: 3 }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
          <Alert severity="info">
            <Typography variant="body2">
              MinutesGen v1.0OpenAI API
              <br />
              API
            </Typography>
          </Alert>

          {error && (
            <Alert severity="error">
              {error}
            </Alert>
          )}

          <TextField
            label="OpenAI API"
            value={apiKey}
            onChange={(e) => setApiKey(e.target.value)}
            type={showApiKey ? 'text' : 'password'}
            fullWidth
            required
            placeholder="sk-..."
            helperText="OpenAIAPI"
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    onClick={() => setShowApiKey(!showApiKey)}
                    edge="end"
                  >
                    {showApiKey ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />

          <Box sx={{ p: 2, backgroundColor: 'grey.50', borderRadius: 1 }}>
            <Typography variant="subtitle2" sx={{ mb: 1, fontWeight: 600 }}>
               API:
            </Typography>
            <Typography variant="body2" component="ol" sx={{ pl: 2 }}>
              <li>OpenAI Platform (platform.openai.com) </li>
              <li></li>
              <li>API Keys</li>
              <li>Create new secret key</li>
              <li></li>
            </Typography>
          </Box>
        </Box>
      </DialogContent>

      <DialogActions sx={{ p: 2 }}>
        <Button onClick={handleClose} disabled={isSaving}>
          
        </Button>
        <Button
          onClick={handleSave}
          variant="contained"
          startIcon={<Save />}
          disabled={!apiKey.trim() || isSaving}
        >
          {isSaving ? '...' : ''}
        </Button>
      </DialogActions>
    </Dialog>
  );
}; 