import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  Alert,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Divider,
  Switch,
  FormControlLabel,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  Settings,
  Save,
  Refresh,
  Info,
  VpnKey,
  Close,
  CheckCircle,
  Error,
} from '@mui/icons-material';
import { APIConfig, getAPIConfig, validateAPIConfig } from '../config/api';

// ===========================================
// MinutesGen v1.0 - API
// ===========================================

interface APISettingsProps {
  open: boolean;
  onClose: () => void;
  onSave: (config: APIConfig) => void;
}

export const APISettings: React.FC<APISettingsProps> = ({ open, onClose, onSave }) => {
  const [config, setConfig] = useState<APIConfig>(getAPIConfig());
  const [validation, setValidation] = useState<{ isValid: boolean; errors: string[] }>({
    isValid: true,
    errors: [],
  });
  const [isTesting, setIsTesting] = useState(false);
  const [testResult, setTestResult] = useState<{ success: boolean; message: string } | null>(null);

  // 
  useEffect(() => {
    if (open) {
      setConfig(getAPIConfig());
      setValidation({ isValid: true, errors: [] });
      setTestResult(null);
    }
  }, [open]);

  // 
  const handleConfigChange = (field: keyof APIConfig, value: any) => {
    const newConfig = { ...config, [field]: value };
    setConfig(newConfig);
    
    // 
    const validation = validateAPIConfig(newConfig);
    setValidation(validation);
  };

  // API
  const testConnection = async () => {
    setIsTesting(true);
    setTestResult(null);

    try {
      // OpenAI API
      const response = await fetch(`${config.baseUrl}/models`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${config.openaiApiKey}`,
          'Content-Type': 'application/json',
        },
      });

      if (response.ok) {
        setTestResult({
          success: true,
          message: 'API',
        });
      } else {
        setTestResult({
          success: false,
          message: `API: ${response.status} ${response.statusText}`,
        });
      }
    } catch (error) {
      setTestResult({
        success: false,
        message: `: ${error instanceof Error ? (error as Error).message : 'Unknown error'}`,
      });
    } finally {
      setIsTesting(false);
    }
  };

  // 
  const handleSave = () => {
    if (validation.isValid) {
      onSave(config);
      onClose();
    }
  };

  // 
  const handleReset = () => {
    setConfig(getAPIConfig());
    setValidation({ isValid: true, errors: [] });
    setTestResult(null);
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="md"
      fullWidth
      PaperProps={{
        sx: {
          borderRadius: 2,
          maxHeight: '80vh',
        },
      }}
    >
      <DialogTitle sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Settings sx={{ mr: 1, color: 'primary.main' }} />
          <Typography variant="h6">API</Typography>
        </Box>
        <IconButton onClick={onClose} size="small">
          <Close />
        </IconButton>
      </DialogTitle>

      <DialogContent dividers sx={{ p: 3 }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
          {/*  */}
          {!validation.isValid && (
            <Alert severity="error">
              <Typography variant="body2" component="div">
                :
                <ul style={{ margin: '8px 0', paddingLeft: '20px' }}>
                  {validation.errors.map((error, index) => (
                    <li key={index}>{error}</li>
                  ))}
                </ul>
              </Typography>
            </Alert>
          )}

          {/*  */}
          <Box>
            <Typography variant="h6" sx={{ mb: 2, display: 'flex', alignItems: 'center' }}>
              <VpnKey sx={{ mr: 1 }} />
              
            </Typography>

            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              <TextField
                label="OpenAI API"
                value={config.openaiApiKey}
                onChange={(e) => handleConfigChange('openaiApiKey', e.target.value)}
                type="password"
                fullWidth
                required
                helperText="OpenAI API"
                error={!config.openaiApiKey && !validation.isValid}
              />

              <TextField
                label="API Base URL"
                value={config.baseUrl}
                onChange={(e) => handleConfigChange('baseUrl', e.target.value)}
                fullWidth
                required
                helperText=""
              />

              <Box sx={{ display: 'flex', gap: 2 }}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    value={config.transcribeModel}
                    label=""
                    onChange={(e) => handleConfigChange('transcribeModel', e.target.value)}
                  >
                    <MenuItem value="gpt-4o-transcribe">GPT-4o Transcribe</MenuItem>
                    <MenuItem value="gpt-4o-mini-transcribe">GPT-4o Mini Transcribe</MenuItem>
                  </Select>
                </FormControl>

                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    value={config.minutesModel}
                    label=""
                    onChange={(e) => handleConfigChange('minutesModel', e.target.value)}
                  >
                    <MenuItem value="o3-mini">o3-mini</MenuItem>
                    <MenuItem value="o4-mini">o4-mini</MenuItem>
                    <MenuItem value="gpt-4.1">GPT-4.1</MenuItem>
                    <MenuItem value="gpt-4o">GPT-4o</MenuItem>
                  </Select>
                </FormControl>

                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    value={config.ttsModel}
                    label=""
                    onChange={(e) => handleConfigChange('ttsModel', e.target.value)}
                  >
                    <MenuItem value="gpt-4o-mini-audio-preview">GPT-4o Mini Audio</MenuItem>
                    <MenuItem value="gpt-4o-mini-realtime-preview">GPT-4o Mini Realtime</MenuItem>
                  </Select>
                </FormControl>
              </Box>
            </Box>
          </Box>

          <Divider />

          {/*  */}
          <Box>
            <Typography variant="h6" sx={{ mb: 2 }}>
              
            </Typography>

            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              <TextField
                label=" (MB)"
                value={Math.round(config.maxFileSize / (1024 * 1024))}
                onChange={(e) => handleConfigChange('maxFileSize', parseInt(e.target.value) * 1024 * 1024)}
                type="number"
                fullWidth
                helperText=""
              />

              <TextField
                label=" ()"
                value={config.timeoutDuration / 1000}
                onChange={(e) => handleConfigChange('timeoutDuration', parseInt(e.target.value) * 1000)}
                type="number"
                fullWidth
                helperText="API "
              />

              <TextField
                label=""
                value={config.retryAttempts}
                onChange={(e) => handleConfigChange('retryAttempts', parseInt(e.target.value))}
                type="number"
                fullWidth
                helperText="API "
              />

              <Box sx={{ display: 'flex', gap: 2 }}>
                <FormControlLabel
                  control={
                    <Switch
                      checked={config.devMode}
                      onChange={(e) => handleConfigChange('devMode', e.target.checked)}
                    />
                  }
                  label=""
                />

                <FormControlLabel
                  control={
                    <Switch
                      checked={config.debugLogs}
                      onChange={(e) => handleConfigChange('debugLogs', e.target.checked)}
                    />
                  }
                  label=""
                />
              </Box>
            </Box>
          </Box>

          <Divider />

          {/*  */}
          <Box>
            <Typography variant="h6" sx={{ mb: 2 }}>
              
            </Typography>

            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
              <Button
                variant="outlined"
                onClick={testConnection}
                disabled={isTesting || !config.openaiApiKey}
                startIcon={isTesting ? <Refresh className="animate-spin" /> : <CheckCircle />}
              >
                {isTesting ? '...' : 'API'}
              </Button>

              <Tooltip title="OpenAI API">
                <Info sx={{ color: 'text.secondary' }} />
              </Tooltip>
            </Box>

            {testResult && (
              <Alert severity={testResult.success ? 'success' : 'error'}>
                {testResult.message}
              </Alert>
            )}
          </Box>
        </Box>
      </DialogContent>

      <DialogActions sx={{ p: 2 }}>
        <Button
          onClick={handleReset}
          startIcon={<Refresh />}
          disabled={isTesting}
        >
          
        </Button>

        <Box sx={{ flexGrow: 1 }} />

        <Button onClick={onClose} disabled={isTesting}>
          
        </Button>

        <Button
          onClick={handleSave}
          variant="contained"
          startIcon={<Save />}
          disabled={!validation.isValid || isTesting}
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  );
}; 