import { useState, useCallback, useEffect, useMemo } from 'react';
import {
  AppState,
  AudioFile,
  ProcessingOptions,
  ProcessingProgress,
  MinutesData,
  AppError,
  ProcessingStage,
  DEFAULT_PROCESSING_OPTIONS,
} from '../types';
import { OpenAIService } from '../services/openai';
import { APIConfig, getAPIConfig, saveAPIConfig } from '../config/api';

// ===========================================
// MinutesGen v1.0 - 
// ===========================================

interface UseAppStateReturn extends AppState {
  apiConfig: APIConfig;
  // 
  setCurrentStep: (step: number) => void;
  setSelectedFile: (file: AudioFile | null) => void;
  setProcessingOptions: React.Dispatch<React.SetStateAction<ProcessingOptions>>;
  setApiConfig: (config: Partial<APIConfig>) => void;
  startProcessing: () => Promise<void>;
  resetState: () => void;
  clearError: () => void;
  
  // 
  canProceedToNextStep: () => boolean;
  getTotalSteps: () => number;
  getStepName: (step: number) => string;
}

export const useAppState = (): UseAppStateReturn => {
  // 
  const [currentStep, setCurrentStep] = useState<number>(0);
  const [selectedFile, setSelectedFile] = useState<AudioFile | null>(null);
  const [processingOptions, setProcessingOptions] = useState<ProcessingOptions>(
    DEFAULT_PROCESSING_OPTIONS
  );
  const [progress, setProgress] = useState<ProcessingProgress | null>(null);
  const [results, setResults] = useState<MinutesData | null>(null);
  const [error, setError] = useState<AppError | null>(null);
  const [isProcessing, setIsProcessing] = useState<boolean>(false);
  const [apiConfig, setApiConfigState] = useState<APIConfig>(getAPIConfig());

  // 
  const stepNames = ['', '', 'AI', ''];

  const openAIService = useMemo(() => new OpenAIService(), []);

  // 
  const setApiConfig = useCallback((newConfig: Partial<APIConfig>) => {
    setApiConfigState(prevConfig => {
      const updatedConfig = { ...prevConfig, ...newConfig };
      saveAPIConfig(updatedConfig);
      return updatedConfig;
    });
  }, []);

  // 
  const clearError = useCallback(() => {
    setError(null);
  }, []);

  // 
  const canProceedToNextStep = useCallback(() => {
    switch (currentStep) {
      case 0: // 
        return selectedFile !== null;
      case 1: // 
        return processingOptions.outputFormats.length > 0;
      case 2: // 
        return false; // 
      case 3: // 
        return false; // 
      default:
        return false;
    }
  }, [currentStep, selectedFile, processingOptions]);

  // 
  const startProcessing = useCallback(async () => {
    if (!selectedFile) {
      setError({
        id: Date.now().toString(),
        code: 'NO_FILE',
        message: '',
        timestamp: new Date(),
        recoverable: true,
      });
      return;
    }

    if (!apiConfig.openaiApiKey) {
      setError({
        id: Date.now().toString(),
        code: 'NO_API_KEY',
        message: 'OpenAI API',
        timestamp: new Date(),
        recoverable: true,
      });
      setCurrentStep(1); // 
      return;
    }

    setIsProcessing(true);
    setError(null);
    setCurrentStep(2); // 

    try {
      // API
      // 
      const transcription = await openAIService.transcribeAudio(
        selectedFile,
        processingOptions,
        setProgress
      );

      // 
      const minutes = await openAIService.generateMinutes(
        transcription,
        processingOptions,
        setProgress
      );

      setResults(minutes);
      setCurrentStep(3); // 

    } catch (err: any) {
      console.error(':', err);
      const errorMessage = err.response?.data?.error?.message || err.message || '';
      setError({
        id: Date.now().toString(),
        code: err.code || 'PROCESSING_FAILED',
        message: `: ${errorMessage}`,
        details: err,
        timestamp: new Date(),
        recoverable: true,
      });
      setCurrentStep(2); // 
    } finally {
      setIsProcessing(false);
    }
  }, [selectedFile, processingOptions, apiConfig, openAIService]);

  // 
  const resetState = useCallback(() => {
    setCurrentStep(0);
    setSelectedFile(null);
    setProcessingOptions(DEFAULT_PROCESSING_OPTIONS);
    setProgress(null);
    setResults(null);
    setError(null);
    setIsProcessing(false);
  }, []);

  // 
  const getTotalSteps = useCallback(() => stepNames.length, []);
  const getStepName = useCallback((step: number) => stepNames[step] || '', []);

  // 10
  useEffect(() => {
    if (error && error.recoverable) {
      const timer = setTimeout(() => {
        clearError();
      }, 10000);
      return () => clearTimeout(timer);
    }
  }, [error, clearError]);

  // 
  return {
    // 
    currentStep,
    selectedFile,
    processingOptions,
    progress,
    results,
    error,
    isProcessing,
    apiConfig,

    // 
    setCurrentStep,
    setSelectedFile,
    setProcessingOptions,
    setApiConfig,
    startProcessing,
    resetState,
    clearError,

    // 
    canProceedToNextStep,
    getTotalSteps,
    getStepName,
  };
};

// ===========================================
// 
// ===========================================

const simulateProcessing = async (
  setProgress: React.Dispatch<React.SetStateAction<ProcessingProgress | null>>
): Promise<void> => {
  const stages: { stage: ProcessingStage; task: string; duration: number }[] = [
    { stage: 'uploading', task: '...', duration: 2000 },
    { stage: 'analyzing', task: '...', duration: 3000 },
    { stage: 'transcribing', task: '...', duration: 8000 },
    { stage: 'generating', task: '...', duration: 5000 },
    { stage: 'formatting', task: '...', duration: 2000 },
    { stage: 'completed', task: '', duration: 0 },
  ];

  const totalDuration = stages.reduce((sum, stage) => sum + stage.duration, 0);
  let elapsedTime = 0;

  for (let i = 0; i < stages.length; i++) {
    const stage = stages[i];
    const startTime = Date.now();

    // 
    setProgress(prev => prev ? {
      ...prev,
      stage: stage.stage,
      currentTask: stage.task,
      percentage: Math.round((elapsedTime / totalDuration) * 100),
      estimatedTimeRemaining: Math.round((totalDuration - elapsedTime) / 1000),
      logs: [
        ...prev.logs,
        {
          id: Date.now().toString(),
          timestamp: new Date(),
          level: 'info',
          message: stage.task,
        },
      ],
    } : null);

    // 
    if (stage.duration > 0) {
      const steps = 10;
      const stepDuration = stage.duration / steps;

      for (let step = 0; step < steps; step++) {
        await new Promise(resolve => setTimeout(resolve, stepDuration));
        elapsedTime += stepDuration;

        setProgress(prev => prev ? {
          ...prev,
          percentage: Math.min(Math.round((elapsedTime / totalDuration) * 100), 100),
          estimatedTimeRemaining: Math.max(Math.round((totalDuration - elapsedTime) / 1000), 0),
        } : null);
      }
    }
  }
}; 