// ===========================================
// MinutesGen v1.0 - OpenAI API 
// ===========================================

import axios, { AxiosInstance } from 'axios';
import { getValidatedAPIConfig } from '../config/api';
import { AudioFile, ProcessingOptions, MinutesData, ProcessingProgress } from '../types';
import { audioProcessor } from './audioProcessor';
// WebCodecsProcessor 
// import { WebCodecsProcessor } from './webCodecsProcessor';

export class OpenAIService {
  private api: AxiosInstance;
  private config;
  /**
   * 80MB 
   * Whisper(GPT-4 Transcribe)  100MB 
   */
  private readonly CHUNK_SIZE_BYTES = 20 * 1024 * 1024; // 20MB  (Whisper25MB)

  constructor() {
    this.config = getValidatedAPIConfig();
    this.api = axios.create({
      baseURL: this.config.baseUrl,
      timeout: this.config.timeoutDuration,
      headers: {
        'Authorization': `Bearer ${this.config.openaiApiKey}`,
      },
    });

    // 
    this.api.interceptors.request.use(
      (config) => {
        if (this.config.debugLogs) {
          console.log('OpenAI API :', config);
        }
        return config;
      },
      (error) => {
        console.error('OpenAI API :', error);
        return Promise.reject(error);
      }
    );

    // 
    this.api.interceptors.response.use(
      (response) => {
        if (this.config.debugLogs) {
          console.log('OpenAI API :', response);
        }
        return response;
      },
      (error) => {
        console.error('OpenAI API :', error);
        
        // 400
        if (error.response) {
          console.error(':', {
            status: error.response.status,
            statusText: error.response.statusText,
            data: error.response.data,
            headers: error.response.headers
          });
        }
        
        return Promise.reject(error);
      }
    );
  }

  /**
   * 
   */
  async transcribeAudio(
    file: AudioFile,
    options: ProcessingOptions,
    onProgress?: (progress: ProcessingProgress) => void
  ): Promise<string> {
    // ----- ffmpeg.wasm -----
    if (file.rawFile && file.rawFile.size > this.CHUNK_SIZE_BYTES) {
      try {
        // ffmpeg
        return await this.transcribeAudioWithFFmpeg(file, options, onProgress);
      } catch (ffError) {
        const errorMessage = `


 :
- ffmpeg.wasm : ${ffError instanceof Error ? ffError.message : ''}
        `.trim();
        throw new Error(errorMessage);
      }
    }
    try {
      // 
      onProgress?.({
        stage: 'transcribing',
        percentage: 10,
        currentTask: 'AI...',
        estimatedTimeRemaining: 0,
        logs: [{
          id: Date.now().toString(),
          timestamp: new Date(),
          level: 'info',
          message: 'AI...',
        }],
        startedAt: new Date(),
      });

      // FormData
      const formData = new FormData();

      //  fetch(blob://...) 
      // FileUpload  rawFile 
      if (file.rawFile) {
        formData.append('file', file.rawFile, file.name);
      } else {
        // 
        const audioBlob = await fetch(file.path, { cache: 'no-store' }).then(r => r.blob());
        formData.append('file', audioBlob, file.name);
      }
      formData.append('model', this.config.transcribeModel);
      formData.append('language', options.language === 'auto' ? '' : options.language);

      const fmt = this.determineResponseFormat(this.config.transcribeModel, options.timestamps);
      if (fmt) {
        formData.append('response_format', fmt);
      }

      // Content-Type boundary 
      const response = await this.api.post('/audio/transcriptions', formData);

      // 
      onProgress?.({
        stage: 'transcribing',
        percentage: 80,
        currentTask: 'AI',
        estimatedTimeRemaining: 0,
        logs: [{
          id: Date.now().toString() + '_1',
          timestamp: new Date(),
          level: 'success',
          message: 'AI',
        }],
        startedAt: new Date(),
      });

      return response.data.text;

    } catch (error) {
      console.error(':', error);
      throw new Error('');
    }
  }

  /**
   * 
   */
  private getAudioDuration(file: File): Promise<number> {
    return new Promise((resolve) => {
      const audio = new Audio();
      let blobUrl: string | null = null;
      
      const cleanup = () => {
        if (blobUrl) {
          URL.revokeObjectURL(blobUrl);
        }
        audio.removeEventListener('loadedmetadata', onLoadedMetadata);
        audio.removeEventListener('error', onError);
        audio.src = '';
      };

      const onLoadedMetadata = () => {
        const duration = audio.duration;
        cleanup();
        resolve(duration && isFinite(duration) ? duration : 0);
      };

      const onError = () => {
        cleanup();
        resolve(0);
      };
      
      audio.addEventListener('loadedmetadata', onLoadedMetadata);
      audio.addEventListener('error', onError);
      
      // 30
      setTimeout(() => {
        cleanup();
        resolve(0);
      }, 30000);
      
      try {
        blobUrl = URL.createObjectURL(file);
        audio.src = blobUrl;
      } catch (error) {
        cleanup();
        resolve(0);
      }
    });
  }

  /**
   * ffmpeg.wasm
   */
  private async transcribeAudioWithFFmpeg(
    file: AudioFile,
    options: ProcessingOptions,
    onProgress?: (progress: ProcessingProgress) => void
  ): Promise<string> {
    if (!file.rawFile) {
      // rawFile 
      return this.transcribeAudio(file, options, onProgress);
    }

    try {
      // ffmpeg.wasm
      const segments = await audioProcessor.processLargeAudioFile(file, 600, onProgress);
      
      let transcript = '';
      
      // 
      for (let i = 0; i < segments.length; i++) {
        const segment = segments[i];
        
        //  ()
        onProgress?.({
          stage: 'transcribing',
          percentage: 70 + Math.round((i / segments.length) * 25),
          currentTask: ` ${i + 1}/${segments.length} AI...`,
          estimatedTimeRemaining: 0,
          logs: [{
            id: Date.now().toString() + '_seg_' + i,
            timestamp: new Date(),
            level: 'info',
            message: ` ${i + 1}/${segments.length} (${segment.startTime.toFixed(1)}s - ${segment.endTime.toFixed(1)}s) AI`,
          }],
          startedAt: new Date(),
        });

        // 
        const segmentFile = new File([segment.blob], segment.name, { type: file.rawFile.type });
        
        const formData = new FormData();
        formData.append('file', segmentFile, segmentFile.name);
        formData.append('model', this.config.transcribeModel);
        formData.append('language', options.language === 'auto' ? '' : options.language);
        const segmentFmt = this.determineResponseFormat(this.config.transcribeModel, options.timestamps);
        if (segmentFmt) {
          formData.append('response_format', segmentFmt);
        }

        // Content-Type boundary 
        const response = await this.api.post('/audio/transcriptions', formData);

        transcript += (response.data.text as string) + '\n';
      }

      //  ()
      onProgress?.({
        stage: 'transcribing',
        percentage: 95,
        currentTask: 'AI',
        estimatedTimeRemaining: 0,
        logs: [{
          id: Date.now().toString() + '_segment_done',
          timestamp: new Date(),
          level: 'success',
          message: 'AI',
        }],
        startedAt: new Date(),
      });

      return transcript.trim();

    } catch (error) {
      console.error('ffmpeg.wasm:', error);
      throw new Error('');
    }
  }

  /**
   * 
   */
  async generateMinutes(
    transcription: string,
    options: ProcessingOptions,
    onProgress?: (progress: ProcessingProgress) => void
  ): Promise<MinutesData> {
    try {
      // 
      onProgress?.({
        stage: 'generating',
        percentage: 20,
        currentTask: 'AI...',
        estimatedTimeRemaining: 0,
        logs: [{
          id: Date.now().toString() + '_2',
          timestamp: new Date(),
          level: 'info',
          message: 'AI',
        }],
        startedAt: new Date(),
      });

      // 
      const prompt = this.buildMinutesPrompt(transcription, options);

      // GPT API
      const response = await this.api.post('/chat/completions', {
        model: this.config.minutesModel,
        messages: [
          {
            role: 'system',
            content: '',
          },
          {
            role: 'user',
            content: prompt,
          },
        ],
        temperature: 0.3,
        max_tokens: 4000,
      });

      // 
      onProgress?.({
        stage: 'generating',
        percentage: 90,
        currentTask: '...',
        estimatedTimeRemaining: 0,
        logs: [{
          id: Date.now().toString() + '_3',
          timestamp: new Date(),
          level: 'success',
          message: 'AI',
        }],
        startedAt: new Date(),
      });

      // 
      const generatedContent = response.data.choices[0].message.content;
      return this.parseGeneratedMinutes(generatedContent, options);

    } catch (error) {
      console.error(':', error);
      throw new Error('');
    }
  }

  /**
   * 
   */
  private buildMinutesPrompt(transcription: string, options: ProcessingOptions): string {
    const basePrompt = `



${transcription}


- 
- 

## 
- : []
- : []
- : []

## 
1. [1]
2. [2]
3. [3]

## 
- []

## 
- []: [] ([])

## TO DO
- [1]
- [2]

## 
- []
`;

    // 
    if (options.customPrompt) {
      return `${basePrompt}\n\n\n${options.customPrompt}`;
    }

    return basePrompt;
  }

  /**
   * 
   */
  private parseGeneratedMinutes(content: string, options: ProcessingOptions): MinutesData {
    // 
    const now = new Date();

    // 
    const minutesData: MinutesData = {
      id: Date.now().toString(),
      title: '',
      date: now,
      duration: 0,
      participants: [
        {
          id: '1',
          name: 'A',
          role: '',
          speakingTime: 0,
        },
      ],
      summary: content.substring(0, 200) + '...',
      keyPoints: [
        {
          id: '1',
          content: '1',
          timestamp: 0,
          importance: 'high',
        },
      ],
      actionItems: [
        {
          id: '1',
          task: '1',
          assignee: 'A',
          priority: 'medium',
          status: 'pending',
          timestamp: 0,
        },
      ],
      transcription: [
        {
          id: '1',
          startTime: 0,
          endTime: 0,
          speakerId: '1',
          text: '',
          confidence: 0.95,
        },
      ],
      outputs: options.outputFormats.map(format => ({
        format,
        content: content,
        generatedAt: now,
        size: content.length,
      })),
      metadata: {
        version: '1.0.0',
        generatedAt: now,
        processingTime: 0,
        tokensUsed: 0,
        model: this.config.minutesModel,
        quality: options.quality,
      },
    };

    return minutesData;
  }

  /**
   *  response_format 
   * - gpt-4o-transcribe  verbose_json   timestamps  "json" 
   * -  (: whisper-1)  timestamps  "verbose_json" 
   * - timestamps  false  (API : text)
   */
  private determineResponseFormat(model: string, timestamps: boolean): string | null {
    if (!timestamps) return null;
    //  gpt-4o-transcribe 
    //  switch
    switch (model) {
      case 'gpt-4o-transcribe':
        return 'json'; // verbose_json 
      default:
        return 'verbose_json';
    }
  }
}

// 
export const openaiService = new OpenAIService(); 