// ===========================================
// MinutesGen v1.0 - Audio Processor Service
// ffmpeg.wasm 
// ===========================================

import { FFmpeg } from '@ffmpeg/ffmpeg';
import { toBlobURL, fetchFile } from '@ffmpeg/util';
import { AudioFile, ProcessingProgress } from '../types';

export interface AudioSegment {
  blob: Blob;
  name: string;
  duration: number;
  startTime: number;
  endTime: number;
}

export class AudioProcessorService {
  private ffmpeg: FFmpeg;
  private isLoaded: boolean = false;
  private loadingPromise: Promise<void> | null = null;
  private preloadAttempted: boolean = false;

  constructor() {
    // Self-hosted public/ffmpeg-core/dist/umd 
    this.ffmpeg = new FFmpeg({
      log: false,
      // ffmpeg-core.js  ()
      corePath: '/ffmpeg-core/dist/umd/ffmpeg-core.js',
    });
    // 
    this.attemptPreload();
  }

  /**
   * ffmpeg.wasm
   */
  private attemptPreload(): void {
    if (this.preloadAttempted) return;
    this.preloadAttempted = true;

    // 5
    setTimeout(() => {
      this.initialize().catch(() => {
        console.log('ffmpeg.wasm ');
      });
    }, 5000);
  }

  /**
   * ffmpeg.wasm 
   */
  async initialize(onProgress?: (progress: ProcessingProgress) => void): Promise<void> {
    if (this.isLoaded) {
      return;
    }

    // Promise
    if (this.loadingPromise) {
      return this.loadingPromise;
    }

    this.loadingPromise = this._initializeFFmpeg(onProgress);
    
    try {
      await this.loadingPromise;
      this.isLoaded = true;
    } catch (error) {
      // Promise
      this.loadingPromise = null;
      throw error;
    }
  }

  private async _initializeFFmpeg(onProgress?: (progress: ProcessingProgress) => void): Promise<void> {
    const attemptLoad = async (
      loader: () => Promise<boolean>,
      sourceName: string,
      timeout: number
    ): Promise<boolean> => {
      onProgress?.({
        stage: 'transcribing',
        percentage: 5,
        currentTask: `${sourceName}...`,
        estimatedTimeRemaining: 0,
        logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'info', message: `${sourceName} ` }],
        startedAt: new Date(),
      });

      const timeoutPromise = new Promise<never>((_, reject) =>
        setTimeout(() => reject(new Error(`${sourceName}ffmpeg.wasm${timeout / 1000}`)), timeout)
      );

      try {
        await Promise.race([loader(), timeoutPromise]);
        onProgress?.({
          stage: 'transcribing',
          percentage: 15,
          currentTask: '',
          estimatedTimeRemaining: 0,
          logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'success', message: `${sourceName} ` }],
          startedAt: new Date(),
        });
        return true;
      } catch (error) {
        console.warn(`${sourceName}`, error);
        onProgress?.({
            stage: 'transcribing',
            percentage: 10,
            currentTask: `(${sourceName})`,
            estimatedTimeRemaining: 0,
            logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'warning', message: `${sourceName} ` }],
            startedAt: new Date(),
        });
        return false;
      }
    };

    // --- 1:  (self-hosted)  ---
    const localLoader = () => this.ffmpeg.load();
    if (await attemptLoad(localLoader, '', 60000)) {
      return; // 
    }

    // --- 2: CDN (unpkg)  ---
    // ffmpeg
    this.ffmpeg = new FFmpeg({ log: false }); 
    const cdnLoader = async () => {
        const baseURL = 'https://unpkg.com/@ffmpeg/core-mt@0.12.6/dist/umd';
        const coreURL = await toBlobURL(`${baseURL}/ffmpeg-core.js`, 'text/javascript');
        const wasmURL = await toBlobURL(`${baseURL}/ffmpeg-core.wasm`, 'application/wasm');
        const workerURL = await toBlobURL(`${baseURL}/ffmpeg-core.worker.js`, 'text/javascript');
        return this.ffmpeg.load({ coreURL, wasmURL, workerURL });
    };
    if (await attemptLoad(cdnLoader, 'CDN', 60000)) {
      return; // 
    }
    
    // ---  ---
    const finalError = new Error('');
    onProgress?.({
        stage: 'error',
        percentage: 100,
        currentTask: '',
        estimatedTimeRemaining: 0,
        logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'error', message: finalError.message }],
        startedAt: new Date(),
    });
    throw finalError;
  }

  /**
   * 
   *
   * @param file - 
   * @param segmentDurationSeconds - 
   * @param onProgress - 
   * @returns 
   *
   * @description
   *  ffmpeg.wasm /
   * 2:
   * 1.  (16-bit PCM WAV) 
   *    
   * 2. ffmpeg  `segment` WAV
   *    `-c copy` 
   *    
   */
  async processLargeAudioFile(
    file: AudioFile,
    segmentDurationSeconds: number = 600, // 10
    onProgress?: (progress: ProcessingProgress) => void
  ): Promise<AudioSegment[]> {
    if (!file.rawFile) {
      throw new Error('');
    }

    await this.initialize(onProgress);

    try {
      const inputFileName = 'input.' + this.getFileExtension(file.name);
      
      onProgress?.({
        stage: 'transcribing',
        percentage: 20,
        currentTask: '...',
        estimatedTimeRemaining: 0,
        logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'info', message: '...' }],
        startedAt: new Date(),
      });
      await this.ffmpeg.writeFile(inputFileName, await fetchFile(file.rawFile));
      
      // ==============================================================
      // 1: 
      // ==============================================================
      const duration = await this.getAudioDuration(inputFileName);
      if (duration === 0) {
        // getAudioDuration0
        // WAV
        try {
          await this.ffmpeg.exec(['-y', '-i', inputFileName, '-vn', '-acodec', 'pcm_s16le', '-t', '1', 'test.wav']);
          await this.ffmpeg.deleteFile('test.wav');
        } catch(e) {
            throw new Error('');
        }
      }
      const totalSegments = Math.ceil(duration / segmentDurationSeconds);

      // ==============================================================
      // 2: 
      // ==============================================================
      onProgress?.({
        stage: 'transcribing',
        percentage: 30,
        currentTask: `AI${totalSegments}...`,
        estimatedTimeRemaining: 0,
        logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'info', message: `: ${Math.round(duration)}: ${totalSegments}AI` }],
        startedAt: new Date(),
      });

      const outputPattern = `segment_%03d.wav`;
      
      // -i: , -f segment: , -segment_time: , -vn: , -acodec...: 
      await this.ffmpeg.exec([
        '-i', inputFileName,
        '-f', 'segment',
        '-segment_time', segmentDurationSeconds.toString(),
        '-vn', // 
        '-acodec', 'pcm_s16le', // 16bitPCM (WAV)
        '-ar', '44100', // 44.1kHz
        '-ac', '1', // 
        '-c:a', 'pcm_s16le', // 
        outputPattern
      ]);

      const segments: AudioSegment[] = [];
      const fileList = await this.ffmpeg.listDir('.');
      
      const segmentFiles = fileList
        .filter(f => f.name.startsWith('segment_') && f.name.endsWith('.wav'))
        .sort((a, b) => a.name.localeCompare(b.name));

      if(segmentFiles.length === 0){
        // 
         onProgress?.({
            stage: 'transcribing',
            percentage: 45,
            currentTask: `AI...`,
            estimatedTimeRemaining: 0,
            logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'info', message: `AI` }],
            startedAt: new Date(),
        });
        const singleOutputName = "segment_000.wav";
        await this.ffmpeg.exec([
          '-i', inputFileName,
          '-vn', '-acodec', 'pcm_s16le', '-ar', '44100', '-ac', '1',
          singleOutputName
        ]);
        segmentFiles.push({ name: singleOutputName, is_dir: false, size: 0 }); // size
      }
      
      let cumulativeTime = 0;
      for (let i = 0; i < segmentFiles.length; i++) {
        const segmentFile = segmentFiles[i];
        
        onProgress?.({
          stage: 'transcribing',
          percentage: 50 + Math.round((i / segmentFiles.length) * 45),
          currentTask: ` ${i + 1}/${segmentFiles.length} ...`,
          estimatedTimeRemaining: 0,
          logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'info', message: ` ${i + 1} ` }],
          startedAt: new Date(),
        });

        const data = await this.ffmpeg.readFile(segmentFile.name);
        const blob = new Blob([data], { type: 'audio/wav' });
        
        await this.ffmpeg.deleteFile(segmentFile.name);
        
        const segmentDuration = await this.getAudioDurationFromBlob(blob);

        segments.push({
          blob,
          name: segmentFile.name,
          duration: segmentDuration,
          startTime: cumulativeTime,
          endTime: cumulativeTime + segmentDuration,
        });
        cumulativeTime += segmentDuration;
      }
      
      // 
      await this.ffmpeg.deleteFile(inputFileName);

      onProgress?.({
        stage: 'transcribing',
        percentage: 95,
        currentTask: '',
        estimatedTimeRemaining: 0,
        logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'success', message: ` ${segments.length} ` }],
        startedAt: new Date(),
      });
      
      return segments;

    } catch (error) {
      console.error('ffmpeg :', error);
      let errorMessage = '';
      if (error instanceof Error) {
        errorMessage = `: ${error.message}`;
      }
      
      onProgress?.({
        stage: 'error',
        percentage: 100,
        currentTask: '',
        estimatedTimeRemaining: 0,
        logs: [{ id: Date.now().toString(), timestamp: new Date(), level: 'error', message: errorMessage }],
        startedAt: new Date(),
      });
      
      throw new Error(errorMessage);
    }
  }

  /**
   * ffmpeg.wasm 
   */
  private async getAudioDuration(fileName: string): Promise<number> {
    let duration = 0;
    let logOutput = '';

    const logListener = ({ message }: { type: string, message: string }) => {
      logOutput += message + '\n';
      const durationMatch = message.match(/Duration: (\d{2}):(\d{2}):(\d{2})\.(\d{2})/);
      if (durationMatch) {
        const hours = parseInt(durationMatch[1], 10);
        const minutes = parseInt(durationMatch[2], 10);
        const seconds = parseInt(durationMatch[3], 10);
        const milliseconds = parseInt(durationMatch[4], 10) * 10;
        duration = hours * 3600 + minutes * 60 + seconds + milliseconds / 1000;
      }
    };

    this.ffmpeg.on('log', logListener);

    try {
      await this.ffmpeg.exec(['-i', fileName, '-f', 'null', '-']);
    } catch (error) {
      // duration
      if (duration === 0) {
        console.error("ffmpeg failed to get duration. Full log:", logOutput);
        // durationOK
      }
    } finally {
      this.ffmpeg.off('log', logListener);
    }
    
    return duration;
  }

  /**
   * Blob
   */
  private getAudioDurationFromBlob(blob: Blob): Promise<number> {
    return new Promise((resolve) => {
      const audio = document.createElement('audio');
      const objectUrl = URL.createObjectURL(blob);
      audio.src = objectUrl;
      audio.addEventListener('loadedmetadata', () => {
        const duration = audio.duration;
        URL.revokeObjectURL(objectUrl);
        resolve(isFinite(duration) ? duration : 0);
      });
      audio.addEventListener('error', () => {
        URL.revokeObjectURL(objectUrl);
        resolve(0);
      });
    });
  }

  /**
   * 
   */
  private getFileExtension(fileName: string): string {
    const lastDot = fileName.lastIndexOf('.');
    return lastDot !== -1 ? fileName.slice(lastDot + 1) : 'mp3';
  }

  /**
   * 
   */
  private getBaseName(fileName: string): string {
    const lastDot = fileName.lastIndexOf('.');
    return lastDot !== -1 ? fileName.slice(0, lastDot) : fileName;
  }

  /**
   * 
   */
  async cleanup(): Promise<void> {
    if (this.isLoaded) {
      try {
        // ffmpeg.wasm 
        await this.ffmpeg.terminate();
        this.isLoaded = false;
        this.loadingPromise = null;
      } catch (error) {
        console.warn('ffmpeg.wasm :', error);
      }
    }
  }
}

// 
export const audioProcessor = new AudioProcessorService(); 